@import url('https://fonts.googleapis.com/css2?family=Exo+2:wght@300;400;700&display=swap');

body {
  font-family: "Exo 2", sans-serif;
  margin: 0px;
  zoom: 0.8;
}

.content-wrapper {
  /* padding: 113px 140px; */
  width: 90%;
  margin: 113px auto;
  display: flex;
  flex-direction: column;
  row-gap: 45px;
}

/* TOP-SECTION */
.top-section {
  display: flex;
  flex-direction: row;
}

.top-section>img {
  flex: 1;
}

.top-section>section {
  flex: 1;
  font-size: 50px;
}

.top-section>section>span {
  font-size: 1em;
  color: #6776af;
  font-weight: bold;
}

.top-section>section>p {
  font-size: 0.5em;
  color: #acacac;
  font-weight: 300;
  line-height: 33px;
  margin: 17px 0px 64px 0;
}

/* FORM */
.form-row {
  display: flex;
  flex-direction: row;
  column-gap: 16px;
}

.form-row:first-child {
  margin-bottom: 38px;
}

.form-group {
  display: flex;
  flex-direction: column;
  flex: 1;
  row-gap: 14px;
}

.form-group>label {
  font-size: 0.5em;
  color: #4a4a4a;
}

.form-group>input {
  height: 50px;
  border-width: 0.5px;
  border-color: #707070;
  border-radius: 5px;
  border-style: solid;
  font-size: 0.3em;
  padding-left: 10px;
  font-family: "Exo 2", sans-serif;
  color: #7895b2;
}

.form-group>input:focus {
  outline-color: transparent;
  outline-style: solid;
  outline-width: 0px;
  border-color: #273685;
  border-radius: 5px;
}

.save-btn {
  background-color: #273685;
  color: white;
  height: 50px;
  width: 260px;
  border-radius: 27.5px;
  float: right;
  margin-top: 27px;
  border: 0;
  font-size: 0.5em;
  font-weight: 300;
  font-family: "Exo 2", sans-serif;
  cursor: pointer;
}

/* BOTTOM-SECTION */
.bottom-section {
  background-color: #f9faff;
  border-radius: 15px;
  box-shadow: 0px 0px 15px rgba(0, 0, 0, 0.08);
  padding: 0px 65px;
}

table {
  width: 100%;
  font-size: 25px;
  border-collapse: collapse;
}

table>thead {
  color: #273685;
  font-size: 1em;
  border-bottom: 1px solid #273685;
  padding: 14px 0;
}

table>thead>tr {}

table>thead>tr>th {
  width: 25%;
  flex: 1;
  height: 67px;
  vertical-align: bottom;
  padding-bottom: 14px;
}

table>thead>tr>th:first-child {
  text-align: left;
}

/* table > thead > tr > th {
    flex: 1;
  }

  table > thead > tr > th:first-child {
    text-align: left;
  }

  table > thead > tr > th:last-child {
    text-align: center;
  } */

tbody>tr {
  border-bottom: 1px solid #d8d8d8;
}

tbody>tr:last-child {
  border-bottom: none;
}

td:first-child {
  text-align: start;
}

td {
  color: #4a4a4a;
  font-size: 0.6em;
  text-align: center;
  height: 80px;
}

/* RESPONSIVE */
@media only screen and (max-width: 1280px) {

  /* TOP-SECTION */
  .top-section {
    display: flex;
    flex-direction: column;
  }
}

@media only screen and (max-width: 480px) {
  .content-wrapper {
    margin: 55px auto;
  }

  .form-group {
    display: flex;
    flex-direction: column;
    flex: 1;
    row-gap: 14px;
  }

  .top-section>section {
    flex: 1;
    font-size: 40px;
  }

  .top-section>section>span {
    font-size: 0.8em;
  }

  .top-section>section>p {
    font-size: 0.5em;
    line-height: 23px;
    margin: 17px 0px 64px 0;
    text-align: justify;
  }

  /* FORM */
  .form-row {
    display: flex;
    flex-direction: column;
    row-gap: 16px;
  }

  .form-row:first-child {
    margin-bottom: 16px;
  }

  form {
    display: flex;
    flex-direction: column;
  }

  .save-btn {
    align-self: center;
    width: 100%;
    font-size: 0.4em;
  }

  .bottom-section {
    overflow: scroll;
    padding: 0px 45px;
  }

  table {
    font-size: 20px;
  }

  table>thead {
    font-size: 0.5em;
    padding: 14px 0;
  }

  table>thead>tr>th {
    width: 25%;
    flex: 1;
    height: 47px;
    vertical-align: bottom;
    padding-bottom: 5px;
  }

  td {
    font-size: 0.4em;
    height: 60px;
  }
}